import { useState, useRef, useEffect, useCallback } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import { Search, Clock, CreditCard, X, Loader2 } from 'lucide-react'
import type { Card } from '../../types/api'

interface SearchSuggestionProps {
    query: string
    cards: Card[]
    isLoading?: boolean
    onSelect: (card: Card) => void
    onClose: () => void
    onQueryChange: (query: string) => void
}

export function SearchSuggestion({
    query,
    cards,
    isLoading = false,
    onSelect,
    onClose,
    onQueryChange
}: SearchSuggestionProps) {
    const { t } = useTranslation()
    const [isFocused, setIsFocused] = useState(false)
    const [recentSearches, setRecentSearches] = useState<string[]>([])
    const inputRef = useRef<HTMLInputElement>(null)
    const containerRef = useRef<HTMLDivElement>(null)

    // 加载最近搜索记录
    useEffect(() => {
        const saved = localStorage.getItem('cardSearchHistory')
        if (saved) {
            setRecentSearches(JSON.parse(saved).slice(0, 5))
        }
    }, [])

    // 点击外部关闭
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
                setIsFocused(false)
            }
        }

        document.addEventListener('mousedown', handleClickOutside)
        return () => document.removeEventListener('mousedown', handleClickOutside)
    }, [])

    // 保存搜索记录
    const saveSearch = useCallback((searchTerm: string) => {
        if (!searchTerm.trim()) return
        const updated = [searchTerm, ...recentSearches.filter(s => s !== searchTerm)].slice(0, 5)
        setRecentSearches(updated)
        localStorage.setItem('cardSearchHistory', JSON.stringify(updated))
    }, [recentSearches])

    const handleSelect = (card: Card) => {
        saveSearch(query)
        onSelect(card)
        setIsFocused(false)
    }

    const handleRecentClick = (term: string) => {
        onQueryChange(term)
        inputRef.current?.focus()
    }

    const clearRecentSearches = () => {
        setRecentSearches([])
        localStorage.removeItem('cardSearchHistory')
    }

    const showDropdown = isFocused && (query.length > 0 || recentSearches.length > 0)

    // 状态颜色映射
    const getStatusColor = (status: string) => {
        switch (status) {
            case 'ACTIVE': return 'bg-success text-success-foreground'
            case 'SUSPENDED': return 'bg-warning text-warning-foreground'
            case 'BLACKLISTED': return 'bg-error text-white'
            case 'EXPIRED': return 'bg-slate-400 text-white'
            default: return 'bg-slate-200 text-slate-700'
        }
    }

    return (
        <div className="relative flex-1 min-w-[300px]" ref={containerRef}>
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400 dark:text-slate-500 pointer-events-none" />
            <input
                ref={inputRef}
                type="text"
                placeholder={t('cards.search.placeholder')}
                value={query}
                onChange={(e) => onQueryChange(e.target.value)}
                onFocus={() => setIsFocused(true)}
                className="w-full pl-10 pr-10 py-2 bg-slate-50 dark:bg-dark-900 border border-slate-200 dark:border-dark-600 rounded-lg text-slate-900 dark:text-slate-200 placeholder-slate-400 dark:placeholder-slate-500 focus:outline-none focus:border-primary-500/50 focus:ring-2 focus:ring-primary-500/20 transition-all"
            />
            {query && (
                <button
                    onClick={() => {
                        onQueryChange('')
                        inputRef.current?.focus()
                    }}
                    className="absolute right-3 top-1/2 -translate-y-1/2 p-1 hover:bg-slate-200 dark:hover:bg-dark-700 rounded transition-colors"
                >
                    <X className="w-4 h-4 text-slate-400" />
                </button>
            )}

            <AnimatePresence>
                {showDropdown && (
                    <motion.div
                        initial={{ opacity: 0, y: -10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -10 }}
                        transition={{ duration: 0.15 }}
                        className="absolute left-0 right-0 top-full mt-2 bg-white dark:bg-dark-800 rounded-xl shadow-xl border border-slate-200 dark:border-dark-600 z-50 overflow-hidden max-h-80 overflow-y-auto"
                    >
                        {/* 搜索结果 */}
                        {query.length > 0 && (
                            <div className="p-2">
                                {isLoading ? (
                                    <div className="flex items-center justify-center py-4 text-slate-500">
                                        <Loader2 className="w-5 h-5 animate-spin mr-2" />
                                        {t('common.loading')}
                                    </div>
                                ) : cards.length > 0 ? (
                                    <>
                                        <div className="px-2 py-1 text-xs font-medium text-slate-400 uppercase tracking-wider">
                                            {t('cards.search.results', { count: cards.length })}
                                        </div>
                                        {cards.slice(0, 8).map((card) => (
                                            <button
                                                key={card.id}
                                                onClick={() => handleSelect(card)}
                                                className="w-full flex items-center gap-3 px-3 py-2 hover:bg-slate-50 dark:hover:bg-dark-700 rounded-lg transition-colors text-left"
                                            >
                                                <div className="p-2 bg-slate-100 dark:bg-dark-600 rounded-lg">
                                                    <CreditCard className="w-4 h-4 text-slate-500" />
                                                </div>
                                                <div className="flex-1 min-w-0">
                                                    <p className="font-mono text-sm text-slate-900 dark:text-slate-200 truncate">
                                                        {card.number}
                                                    </p>
                                                    <p className="text-xs text-slate-500 truncate">
                                                        {t(`cards.types.${card.type}`)} • ¥{card.balance.toLocaleString()}
                                                    </p>
                                                </div>
                                                <span className={`px-2 py-0.5 rounded-full text-[10px] font-bold uppercase ${getStatusColor(card.status)}`}>
                                                    {t(`cards.status.${card.status}`)}
                                                </span>
                                            </button>
                                        ))}
                                    </>
                                ) : (
                                    <div className="flex flex-col items-center py-6 text-slate-500">
                                        <Search className="w-8 h-8 mb-2 opacity-50" />
                                        <p className="text-sm">{t('cards.search.noResults')}</p>
                                    </div>
                                )}
                            </div>
                        )}

                        {/* 最近搜索 */}
                        {query.length === 0 && recentSearches.length > 0 && (
                            <div className="p-2">
                                <div className="flex items-center justify-between px-2 py-1">
                                    <span className="text-xs font-medium text-slate-400 uppercase tracking-wider flex items-center gap-1">
                                        <Clock className="w-3 h-3" />
                                        {t('cards.search.recentSearches')}
                                    </span>
                                    <button
                                        onClick={clearRecentSearches}
                                        className="text-xs text-slate-400 hover:text-slate-600 transition-colors"
                                    >
                                        {t('common.clear')}
                                    </button>
                                </div>
                                {recentSearches.map((term, index) => (
                                    <button
                                        key={index}
                                        onClick={() => handleRecentClick(term)}
                                        className="w-full flex items-center gap-3 px-3 py-2 hover:bg-slate-50 dark:hover:bg-dark-700 rounded-lg transition-colors text-left"
                                    >
                                        <Clock className="w-4 h-4 text-slate-400" />
                                        <span className="text-sm text-slate-600 dark:text-slate-300">{term}</span>
                                    </button>
                                ))}
                            </div>
                        )}
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    )
}
