import { useState, useRef, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
    MoreHorizontal,
    ArrowDownLeft,
    Lock,
    RefreshCw,
    Eye,
    Ban,
    X
} from 'lucide-react'
import type { Card } from '../../types/api'

interface QuickActionsMenuProps {
    card: Card
    onTopUp: () => void
    onSuspend: () => void
    onActivate: () => void
    onBlacklist: () => void
    onReplace: () => void
    onView: () => void
    disabled?: boolean
}

export function QuickActionsMenu({
    card,
    onTopUp,
    onSuspend,
    onActivate,
    onBlacklist,
    onReplace,
    onView,
    disabled = false
}: QuickActionsMenuProps) {
    const { t } = useTranslation()
    const [isOpen, setIsOpen] = useState(false)
    const menuRef = useRef<HTMLDivElement>(null)

    // 点击外部关闭菜单
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
                setIsOpen(false)
            }
        }

        if (isOpen) {
            document.addEventListener('mousedown', handleClickOutside)
        }

        return () => {
            document.removeEventListener('mousedown', handleClickOutside)
        }
    }, [isOpen])

    const handleAction = (action: () => void) => {
        action()
        setIsOpen(false)
    }

    const menuItems = [
        // 查看详情 - 始终显示
        {
            label: t('common.details'),
            icon: Eye,
            onClick: () => handleAction(onView),
            show: true,
            className: 'text-slate-600 dark:text-slate-300 hover:bg-slate-100 dark:hover:bg-dark-700'
        },
        // 充值 - 仅正常状态
        {
            label: t('cards.topUp'),
            icon: ArrowDownLeft,
            onClick: () => handleAction(onTopUp),
            show: card.status === 'ACTIVE',
            className: 'text-success hover:bg-success/10'
        },
        // 挂失 - 仅正常状态
        {
            label: t('cards.suspend'),
            icon: Lock,
            onClick: () => handleAction(onSuspend),
            show: card.status === 'ACTIVE',
            className: 'text-warning hover:bg-warning/10'
        },
        // 解挂 - 仅挂失状态
        {
            label: t('cards.activate'),
            icon: RefreshCw,
            onClick: () => handleAction(onActivate),
            show: card.status === 'SUSPENDED',
            className: 'text-success hover:bg-success/10'
        },
        // 补换卡 - 非黑名单
        {
            label: t('cards.actions.replace'),
            icon: RefreshCw,
            onClick: () => handleAction(onReplace),
            show: card.status !== 'BLACKLISTED',
            className: 'text-primary-600 dark:text-primary-400 hover:bg-primary-500/10'
        },
        // 拉黑 - 非黑名单
        {
            label: t('cards.blacklist'),
            icon: Ban,
            onClick: () => handleAction(onBlacklist),
            show: card.status !== 'BLACKLISTED',
            className: 'text-error hover:bg-error/10'
        }
    ]

    const visibleItems = menuItems.filter(item => item.show)

    return (
        <div className="relative" ref={menuRef}>
            <button
                onClick={(e) => {
                    e.stopPropagation()
                    setIsOpen(!isOpen)
                }}
                disabled={disabled}
                className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors text-slate-500 dark:text-slate-400 hover:text-slate-900 dark:hover:text-slate-200 disabled:opacity-50"
                aria-label={t('cards.quickActions.title')}
            >
                <MoreHorizontal className="w-4 h-4" />
            </button>

            <AnimatePresence>
                {isOpen && (
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95, y: -10 }}
                        animate={{ opacity: 1, scale: 1, y: 0 }}
                        exit={{ opacity: 0, scale: 0.95, y: -10 }}
                        transition={{ duration: 0.15 }}
                        className="absolute right-0 top-full mt-1 w-48 bg-white dark:bg-dark-800 rounded-xl shadow-xl border border-slate-200 dark:border-dark-600 z-50 overflow-hidden"
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className="p-1">
                            <div className="px-3 py-2 text-xs font-medium text-slate-400 dark:text-slate-500 uppercase tracking-wider flex items-center justify-between">
                                {t('cards.quickActions.title')}
                                <button
                                    onClick={() => setIsOpen(false)}
                                    className="p-1 hover:bg-slate-100 dark:hover:bg-dark-700 rounded"
                                >
                                    <X className="w-3 h-3" />
                                </button>
                            </div>

                            {visibleItems.map((item, index) => (
                                <button
                                    key={index}
                                    onClick={item.onClick}
                                    className={`w-full flex items-center gap-3 px-3 py-2 text-sm font-medium rounded-lg transition-colors ${item.className}`}
                                >
                                    <item.icon className="w-4 h-4" />
                                    {item.label}
                                </button>
                            ))}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    )
}
