import React, { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { FileText, Camera, UserCheck, ShieldCheck, Loader2, X, ChevronRight, Upload } from 'lucide-react'
import { useTranslation } from 'react-i18next'
import { useKyc } from '../../hooks/useTransit'

type Step = 'UPLOAD' | 'OCR' | 'FACE_MATCH' | 'VERIFY' | 'SUCCESS'

interface KycWizardProps {
    onClose: () => void
    onSuccess: () => void
}

export function KycWizard({ onClose, onSuccess }: KycWizardProps) {
    const { t } = useTranslation()
    const [step, setStep] = useState<Step>('UPLOAD')
    const kycMutation = useKyc()

    const handleNext = async () => {
        const nextSteps: Record<Step, Step> = {
            'UPLOAD': 'OCR',
            'OCR': 'FACE_MATCH',
            'FACE_MATCH': 'VERIFY',
            'VERIFY': 'SUCCESS',
            'SUCCESS': 'SUCCESS'
        }

        try {
            if (step !== 'SUCCESS') {
                await kycMutation.mutateAsync(step as any)
                const next = nextSteps[step]
                setStep(next)
                if (next === 'SUCCESS') {
                    setTimeout(onSuccess, 2000)
                }
            }
        } catch (err) {
            console.error('KYC Error:', err)
        }
    }

    const steps = [
        { id: 'UPLOAD', icon: Upload, label: t('kyc.steps.upload') },
        { id: 'OCR', icon: FileText, label: t('kyc.steps.ocr') },
        { id: 'FACE_MATCH', icon: Camera, label: t('kyc.steps.faceMatch') },
        { id: 'VERIFY', icon: UserCheck, label: t('kyc.steps.verify') },
    ]

    return (
        <div className="flex flex-col h-full">
            <div className="flex items-center justify-between mb-8">
                <h2 className="text-xl font-display font-bold text-slate-900 dark:text-slate-100">{t('kyc.title')}</h2>
                <button onClick={onClose} className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg">
                    <X className="w-5 h-5" />
                </button>
            </div>

            <div className="flex justify-between mb-8 relative">
                <div className="absolute top-1/2 left-0 w-full h-0.5 bg-slate-100 dark:bg-dark-800 -translate-y-1/2 -z-10" />
                {steps.map((s, i) => {
                    const Icon = s.icon
                    const isActive = step === s.id
                    const isCompleted = steps.findIndex(x => x.id === step) > i || step === 'SUCCESS'

                    return (
                        <div key={s.id} className="flex flex-col items-center gap-2">
                            <div className={`w-10 h-10 rounded-full flex items-center justify-center transition-all duration-500 ${isActive ? 'bg-primary-500 text-white ring-4 ring-primary-500/20' :
                                    isCompleted ? 'bg-success text-white' : 'bg-white dark:bg-dark-800 text-slate-400 border border-slate-200 dark:border-dark-700'
                                }`}>
                                {isCompleted ? <ShieldCheck className="w-5 h-5" /> : <Icon className="w-5 h-5" />}
                            </div>
                            <span className={`text-[10px] font-medium uppercase tracking-wider ${isActive ? 'text-primary-600' : 'text-slate-500'}`}>
                                {s.label}
                            </span>
                        </div>
                    )
                })}
            </div>

            <div className="flex-1 flex flex-col items-center justify-center p-6 bg-slate-50 dark:bg-dark-900/50 rounded-2xl border border-dashed border-slate-200 dark:border-dark-700">
                <AnimatePresence mode="wait">
                    {step === 'UPLOAD' && (
                        <motion.div
                            key="upload"
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -10 }}
                            className="text-center"
                        >
                            <div className="w-20 h-20 bg-primary-500/10 text-primary-600 rounded-2xl flex items-center justify-center mx-auto mb-4">
                                <Upload className="w-10 h-10" />
                            </div>
                            <h3 className="text-lg font-bold text-slate-900 dark:text-slate-100 mb-2">{t('kyc.upload.title')}</h3>
                            <p className="text-sm text-slate-500 mb-6">{t('kyc.upload.hint')}</p>
                            <button onClick={handleNext} className="btn-primary w-full max-w-xs">{t('kyc.upload.selectFile')}</button>
                        </motion.div>
                    )}

                    {kycMutation.isPending && (
                        <motion.div
                            key="loading"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            className="text-center"
                        >
                            <Loader2 className="w-12 h-12 animate-spin text-primary-600 mx-auto mb-4" />
                            <p className="text-slate-600 dark:text-slate-400 font-medium">
                                {step === 'OCR' ? t('kyc.loading.ocr') : step === 'FACE_MATCH' ? t('kyc.loading.faceMatch') : t('kyc.loading.verify')}
                            </p>
                        </motion.div>
                    )}

                    {step === 'SUCCESS' && (
                        <motion.div
                            key="success"
                            initial={{ scale: 0.9, opacity: 0 }}
                            animate={{ scale: 1, opacity: 1 }}
                            className="text-center"
                        >
                            <div className="w-20 h-20 bg-success/10 text-success rounded-full flex items-center justify-center mx-auto mb-4">
                                <ShieldCheck className="w-12 h-12" />
                            </div>
                            <h3 className="text-xl font-bold text-slate-900 dark:text-slate-100 mb-2">{t('kyc.success.title')}</h3>
                            <p className="text-sm text-slate-500">{t('kyc.success.description')}</p>
                        </motion.div>
                    )}
                </AnimatePresence>

                {step !== 'UPLOAD' && step !== 'SUCCESS' && !kycMutation.isPending && (
                    <button onClick={handleNext} className="btn-primary flex items-center gap-2 px-8 mt-6">
                        {t('kyc.actions.continue')} <ChevronRight className="w-4 h-4" />
                    </button>
                )}
            </div>
        </div>
    )
}
