import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import { AlertTriangle, CheckCircle2, XCircle, Info, Loader2, Lightbulb, Eye, EyeOff } from 'lucide-react';

type ConfirmType = 'info' | 'success' | 'warning' | 'error' | 'danger';

interface SmartConfirmDialogProps {
  isOpen: boolean;
  type: ConfirmType;
  title: string;
  message: string;
  details?: string;
  impact?: { type: 'positive' | 'negative' | 'neutral'; description: string }[];
  tips?: string[];
  requireConfirmation?: boolean;
  confirmationText?: string;
  countdown?: number;
  onConfirm: () => void | Promise<void>;
  onCancel: () => void;
  loading?: boolean;
}

export function SmartConfirmDialog({
  isOpen,
  type,
  title,
  message,
  details,
  impact,
  tips,
  requireConfirmation = false,
  confirmationText,
  countdown = 0,
  onConfirm,
  onCancel,
  loading = false,
}: SmartConfirmDialogProps) {
  const { t } = useTranslation('help');
  const [countdownValue, setCountdownValue] = useState(countdown);
  const [isCountingDown, setIsCountingDown] = useState(countdown > 0);
  const [showDetails, setShowDetails] = useState(false);
  const [confirmed, setConfirmed] = useState(false);

  useEffect(() => {
    if (countdown > 0 && isOpen) {
      setIsCountingDown(true);
      const timer = setInterval(() => {
        setCountdownValue((prev) => {
          if (prev <= 1) {
            setIsCountingDown(false);
            clearInterval(timer);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
      return () => clearInterval(timer);
    }
  }, [countdown, isOpen]);

  const config = {
    info: { icon: Info, bgColor: 'bg-blue-600', borderColor: 'border-blue-200' },
    success: { icon: CheckCircle2, bgColor: 'bg-success', borderColor: 'border-success/30' },
    warning: { icon: AlertTriangle, bgColor: 'bg-warning', borderColor: 'border-warning/30' },
    error: { icon: XCircle, bgColor: 'bg-error', borderColor: 'border-error/30' },
    danger: { icon: AlertTriangle, bgColor: 'bg-error', borderColor: 'border-error/30' },
  };

  const { icon: Icon, bgColor, borderColor } = config[type];
  const canConfirm = !isCountingDown && (!requireConfirmation || confirmed);

  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={onCancel}
            className="absolute inset-0 bg-black/60 backdrop-blur-sm"
          />
          <motion.div
            initial={{ opacity: 0, scale: 0.9, y: 20 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.9, y: 20 }}
            className="relative bg-white dark:bg-dark-800 rounded-2xl shadow-2xl w-full max-w-lg overflow-hidden"
          >
            <div className={`p-6 ${bgColor} text-white`}>
              <div className="flex items-start gap-4">
                <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center flex-shrink-0">
                  <Icon className="w-6 h-6" />
                </div>
                <div className="flex-1">
                  <h3 className="text-xl font-bold">{title}</h3>
                  <p className="text-white/80 mt-1">{message}</p>
                </div>
              </div>
            </div>

            <div className="p-6 space-y-4">
              {details && (
                <div className="bg-slate-50 rounded-xl p-4">
                  <button
                    onClick={() => setShowDetails(!showDetails)}
                    className="flex items-center gap-2 text-sm font-medium text-slate-600"
                  >
                    {showDetails ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                    {showDetails ? t('dialog.hideDetails') : t('dialog.showDetails')}
                  </button>
                  <AnimatePresence>
                    {showDetails && (
                      <motion.div
                        initial={{ height: 0, opacity: 0 }}
                        animate={{ height: 'auto', opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                        className="mt-3 text-sm text-slate-600"
                      >
                        {details}
                      </motion.div>
                    )}
                  </AnimatePresence>
                </div>
              )}

              {impact && impact.length > 0 && (
                <div className="space-y-2">
                  <h4 className="text-sm font-semibold text-slate-700">{t('dialog.impactAnalysis')}</h4>
                  <div className="space-y-2">
                    {impact.map((item, idx) => (
                      <div
                        key={idx}
                        className={`flex items-start gap-2 p-3 rounded-lg ${
                          item.type === 'positive'
                            ? 'bg-success/10 text-success'
                            : item.type === 'negative'
                            ? 'bg-error/10 text-error'
                            : 'bg-slate-100 text-slate-600'
                        }`}
                      >
                        <div
                          className={`w-2 h-2 rounded-full mt-1.5 ${
                            item.type === 'positive' ? 'bg-success' : item.type === 'negative' ? 'bg-error' : 'bg-slate-400'
                          }`}
                        />
                        <span className="text-sm">{item.description}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {tips && tips.length > 0 && (
                <div className="bg-primary-50 rounded-xl p-4">
                  <div className="flex items-center gap-2 mb-2">
                    <Lightbulb className="w-4 h-4 text-primary-600" />
                    <span className="text-sm font-medium text-primary-700">{t('dialog.tips')}</span>
                  </div>
                  <ul className="space-y-1">
                    {tips.map((tip, idx) => (
                      <li key={idx} className="text-sm text-primary-600/80 flex items-start gap-2">
                        <span>•</span>
                        <span>{tip}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {requireConfirmation && (
                <label className="flex items-start gap-3 p-4 border border-slate-200 rounded-xl cursor-pointer hover:bg-slate-50">
                  <input
                    type="checkbox"
                    checked={confirmed}
                    onChange={(e) => setConfirmed(e.target.checked)}
                    className="mt-1 w-4 h-4 rounded border-slate-300 text-primary-600"
                  />
                  <span className="text-sm text-slate-700">{confirmationText || t('dialog.defaultConfirmation')}</span>
                </label>
              )}

              {isCountingDown && countdownValue > 0 && (
                <div className="text-center text-sm text-slate-500">{t('dialog.countdown', { seconds: countdownValue })}</div>
              )}
            </div>

            <div className="p-6 pt-0 flex gap-3">
              <button
                onClick={onCancel}
                disabled={loading}
                className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl font-medium hover:bg-slate-50 disabled:opacity-50"
              >
                {t('dialog.cancel')}
              </button>
              <button
                onClick={onConfirm}
                disabled={!canConfirm || loading}
                className={`flex-1 px-4 py-3 rounded-xl font-medium flex items-center justify-center gap-2 disabled:opacity-50 ${
                  type === 'danger' ? 'bg-error text-white hover:bg-error-dark' : `${bgColor} text-white hover:opacity-90`
                }`}
              >
                {loading ? (
                  <>
                    <Loader2 className="w-4 h-4 animate-spin" />
                    {t('dialog.processing')}
                  </>
                ) : isCountingDown ? (
                  <>{countdownValue}s</>
                ) : (
                  <>{type === 'danger' ? t('dialog.confirmDanger') : t('dialog.confirm')}</>
                )}
              </button>
            </div>
          </motion.div>
        </div>
      )}
    </AnimatePresence>
  );
}

export function useSmartConfirm() {
  const [config, setConfig] = useState<SmartConfirmDialogProps | null>(null);

  const confirm = (props: Omit<SmartConfirmDialogProps, 'isOpen' | 'onConfirm' | 'onCancel'>) => {
    return new Promise<boolean>((resolve) => {
      setConfig({
        ...props,
        isOpen: true,
        onConfirm: () => {
          resolve(true);
          setConfig(null);
        },
        onCancel: () => {
          resolve(false);
          setConfig(null);
        },
      });
    });
  };

  return { confirm, dialogProps: config };
}
